<template>
  <div v-if="visible">
    <div class="main__container">
        <div class="main__header">
            <img :src="require('@/assets/logo.png')" alt="">
            <h1>Garage</h1>
            <div class="flex__header">
                <div class="main__header-right">
                    <div :class="search ? 'main__search active' : 'main__search'" id="main__search-input">
                        <i @click="changesearchstate()" class="fas fa-search"></i>
                        <input type="text" placeholder="Suche" v-model="searching" :style="{display: search ? 'block' : 'none'}" id="input">
                    </div>
                    <div class="main__fav" @click="changefavstate()">
                        <i class="fas fa-bookmark" :style="{color: showonlyfav ? 'var(--clr-purple)' : 'var(--clr-text)'}"></i>
                    </div>
                </div>
                <div class="main__header-right">
                    <div :class="state == 'einparken' ? 'main__header-parkin active__park' : 'main__header-parkin'" @click="changestate('einparken')">
                        <i class="fas fa-garage"></i>
                        <p>Einparken</p>
                    </div>
                    <div :class="state == 'ausparken' ? 'main__header-parkin active__park' : 'main__header-parkin'" @click="changestate('ausparken')">
                        <i class="fad fa-garage-open"></i>
                        <p>Ausparken</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="main__content">
            <div class="overflow__container">
                <div class="item__grid" id="item__grid">
                    <div class="grid__item" v-for="(car, index) in vehiclelist" :key="index" :style="{display: getdata(index)}">
                        <div v-if="car.showrenamewindow == false">
                          <div class="rename__button" @click="car.showrenamewindow = true">
                            <i class="far fa-edit"></i>
                          </div>
                          <h1 class="car__header">{{car.model}}</h1>
                          <h2>Name: <span>{{car.nickname}}</span></h2>
                          <div class="button__container">
                              <button @click="stateaction(index)"><p>{{state}}</p></button>
                              <button @click="favourite(index)"><i class="fas fa-bookmark" :style="{color: car.isfav ? 'var(--clr-purple)' : 'var(--clr-text)'}"></i></button>
                          </div>
                        </div>
                        <div v-else>
                          <div class="vehiclerename__window">
                            <div class="vehiclerename__header">
                              <i class="fas fa-arrow-left" @click="car.showrenamewindow = false"></i>
                              <h1>Fahrzeug umbennen</h1>
                            </div>
                            <input maxlength="10" class="vehiclerename__input" :id="car.plate" placeholder="Name">
                            <button style="margin-top: 1.3vh;" @click="rename(index, car.plate)"><p>Umbennen</p></button>
                          </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      visible: false,
      search: false,
      searching : "",
      state: 'einparken',
      showonlyfav: false,
      vehiclelist: [],
      cooldown: false,
      gotresponse: true
    }
  },
  methods: {
    changestate(state) {
      if (this.gotresponse == true) {
        if (this.state != state) {
          this.state = state
          this.vehiclelist = []
          this.gotresponse = false
          if (this.state == 'einparken') {
            fetch("https://lifev_garage/enable-parking", {method: "post"})
          } else if (this.state == 'ausparken') {
            fetch("https://lifev_garage/enable-parkout", {method: "post"})
          }
        }
      }
    },
    close() {
      this.visible = false
      this.vehiclelist = []
      this.search = false
      this.searching = ""
      this.state = "einparken"
      this.gotresponse = true
      fetch("https://lifev_garage/escape", {method: "post"})
    },
    changefavstate() {
      if(this.showonlyfav) {
        this.showonlyfav = false
      } else {
        this.showonlyfav = true
      }
    },
    favourite(index) {
      if (this.cooldown) {
          fetch("https://lifev_garage/notify", {
          method: "post",
          body: JSON.stringify({
            text: "Bitte warten sie einen moment...",
          })
        })
        return
      }
      if(this.vehiclelist[index].isfav) {
        this.vehiclelist[index].isfav = false
        fetch("https://lifev_garage/setvehfav", {
          method: "post",
          body: JSON.stringify({
            plate: this.vehiclelist[index].plate,
            state: false
          })
        })
        this.cooldown = true
        setTimeout(() => {
          this.cooldown = false
        }, 10000)
      } else {
        this.vehiclelist[index].isfav = true
        fetch("https://lifev_garage/setvehfav", {
          method: "post",
          body: JSON.stringify({
            plate: this.vehiclelist[index].plate,
            state: true
          })
        })
        this.cooldown = true
        setTimeout(() => {
          this.cooldown = false
        }, 10000)
      }
    },
    changesearchstate() {
      if (this.search) {
        this.search = false
        this.searching = ""
      } else {
        this.search = true
      }
    },
    getdata(index) {
      const modelname = this.vehiclelist[index].model.toUpperCase()
      const nickname = this.vehiclelist[index].nickname.toUpperCase()

      if (this.showonlyfav == true) {
        if (this.searching == '') {
          if (this.vehiclelist[index].isfav) {
            return 'block'
          } else {
            return 'none'
          }
        } else if (modelname.toUpperCase().indexOf(this.searching.toUpperCase()) > -1 || nickname.toUpperCase().indexOf(this.searching.toUpperCase()) > -1) {
          if (this.vehiclelist[index].isfav) {
            return 'block'
          } else {
            return 'none'
          }
        } else {
            return 'none'
        }
      } else {
        if (this.searching == '') {
          return 'block'
        } else if (modelname.toUpperCase().indexOf(this.searching.toUpperCase()) > -1 || nickname.toUpperCase().indexOf(this.searching.toUpperCase()) > -1) {
          return 'block'
        } else {
          return 'none'
        }
      }
    },
    rename(index, plate) {
      const input = document.getElementById(plate).value

      if (this.cooldown) {
        fetch("https://lifev_garage/notify", {
          method: "post",
          body: JSON.stringify({
            text: "Bitte warten sie einen moment...",
          })
        })
        return
      }

      if (input != "") {
        fetch("https://lifev_garage/rename", {
          method: "post",
          body: JSON.stringify({
            plate: plate,
            nickname: input
          })
        })

        this.vehiclelist[index].nickname = input
        this.vehiclelist[index].showrenamewindow = false
        this.cooldown = true
        setTimeout(() => {
          this.cooldown = false
        }, 10000)
      }
    },
    stateaction(index) {
      if(this.state == "einparken") {
        fetch("https://lifev_garage/park-in", {
          method: "post",
          body: JSON.stringify({
            plate: this.vehiclelist[index].plate
          })
        })
      } else if (this.state == "ausparken") {
        fetch("https://lifev_garage/park-out", {
          method: "post",
          body: JSON.stringify({
            plate: this.vehiclelist[index].plate
          })
        })
      }
      this.close()
    }
  },
  mounted() {
    window.addEventListener('message', (e) => {
      var event = e.data
      if (event.action == "show") {
        if (event.state) {
          this.visible = true
          fetch("https://lifev_garage/enable-parking", {method: "post"})
        } else {
          this.close()
        }
      } else if (event.action == "setcars") {
        this.vehiclelist.push(JSON.parse(event.data))
        this.gotresponse = true
      }
    });
  },
  created() {
    let that = this;

    document.addEventListener('keyup', function (evt) {
        if (that.visible) {
          if (evt.keyCode === 27) {
            that.close()
          }
        }
    });
  }
}
</script>

